/***/ (function(module, exports) {

	module.exports = {"data":{"site":{"siteMetadata":{"title":"Funny Side Up","author":"Santosh Venkatraman"}},"markdownRemark":{"id":"/Users/santoshvenkatraman/Personal/Blog/New Folder With Items/blog/src/pages/looking-back-at-2017.md absPath of file >>> MarkdownRemark","html":"<p>2017 started off on a noisy note thanks to people blasting a LOT of crackers. So the backstreet dogs decided to howl before their standard 3AM standup meeting. Just like other people, I happened to go through <em>ups</em> and <em>downs</em> in this year. But unlike other people, I am going to label them as <em>lessons learnt</em> and nothing else.</p>\n<h3>Play time</h3>\n<p>I finally got to act in a play that was 80 minutes long. It was about 5 people in a line and their struggles of coming first in it. We had two shows - one in Bangalore and another in Madras under the banner of CreaShakthi, a prominent theater group in Madras. In spite of living in different continents of Bangalore, we managed to practice quite a bit before the play. All the credits go to my friends who trained me.</p>\n<h3>Musings</h3>\n<p>This year has been quite eventful as a developer -</p>\n<ul>\n<li>\n<p>Learnt the hard way - <strong>Code is written for other humans to read. With enough documentation and context, if people are unable to understand what you have tried to express in your code, you have failed.</strong></p>\n</li>\n<li>\n<p><strong>Started working with React, Webpack and SASS</strong>\nComing from a (non-Javascript) Python background, I found the learning curve for React to be steep. Especially wrapping around the concepts of UI as (almost) pure functions. Learning Webpack was a hard challenge, initially. I gave up in February until I tried again in July for one of my side projects. SASS was fun to learn. It enabled writing maintainable code.</p>\n</li>\n<li>\n<p><strong>Started working with React Native</strong>\nThe main challenge of React Native is related to the initial setup of Android SDKs and XCode, a few months later. Since React Native is a framework based on React, it was not a steep learning curve as I expected. For people wondering if choosing React Native would speed things up development wise - it <strong>totally</strong> depends on the people involved.</p>\n</li>\n<li>\n<p><strong>Learnt about the whole lifecycle of product development</strong>\nIt starts with discussing what <em>features</em> are required in said product. After said heated discussions it formally becomes a <em>product requirements document</em> which becomes my <em>source of truth</em>. Sometime <em>design mocks</em> also come through to you. After that it’s all about <em>planning</em>, <em>project structuring</em>, <em>chunking down objectives</em> into <em>tasks with deadlines</em>, <em>setting a deadline</em>, <em>being accountable with regular updates to whoever you’re reporting to</em> and <em>delivering it before or on agreed delivery date</em>.</p>\n</li>\n<li>\n<p><strong>Launched my side project web app called FootyFollowr</strong>\nI finally got around to solve a problem I faced ever since I graduated in 2015 - following football leagues without the fuss. This <a href=\"/why-footyfollower\">post</a> explains it better.</p>\n</li>\n<li>\n<p>Lesson learnt - <strong>If you’re planning on launching an idea of yours, launch a functional version first, get critical feedback and iterate on it. Chasing perfection without launching or iterating on a lauched version is useless.</strong>\nThis applies to any idea - it may be a side project or it might related to work.</p>\n</li>\n<li>\n<p><strong>Code reviews are useful</strong>\nCode reviews are useful if the people involved are objective in their criticisms. Otherwise it’s just pain and frustration. Especially if you’re dealing with someone obsessed with single quotes, commit squashing and incomprehensible commit messages that are of no use to anybody reading it.</p>\n</li>\n<li>\n<p><strong>Burning out is common</strong>\nIf you don’t know what a burnout is, Psychology Today has succintly explained it <a href=\"https://www.psychologytoday.com/basics/burnout\">here</a>. Usually taking a break and/or spending time with family helps you get out of one. But if the issue you’re dealing with is something deeper, then you have to address the issue before taking any break that’d be useful. If the issue is related to people who are incapable to or won’t address the issue, fortunately you’re not alone. Ignore anyone who suggests allopathic “solutions” to your problem in the pretext of solving it.</p>\n</li>\n<li>\n<p><strong>Context switching is hard</strong>\nIf you’re not sure what I am talking about read <a href=\"http://i.imgur.com/3uyRWGJ.jpg\">this comic</a>. If you’re still unsure, please read the reddit <a href=\"https://www.reddit.com/r/ProgrammerHumor/comments/2rmir6/why_developers_hate_being_interrupted/\">thread</a> associated with said comic and this <a href=\"https://www.joelonsoftware.com/2001/02/12/human-task-switches-considered-harmful/\">article</a> by Joel Spolsky. Non-technical folks won’t get the pain of context switching. Heck they think it’s a joke.</p>\n</li>\n<li>\n<p><strong>Blogging is fun and useful</strong>\nAfter years of contemplation, I have started to blog mostly about my musings as developer <a href=\"https://goo.gl/BPqZdZ\">here</a>. It is fun and useful because I am documenting things that I encounter.</p>\n</li>\n</ul>\n<h3>People</h3>\n<ul>\n<li>\n<p>I recommend everybody <strong>study</strong> Dale Carnegie’s <strong>How to win friends and influence people</strong>, remember his teachings and apply it when dealing with people. This is a note to self.</p>\n</li>\n<li>\n<p>Be objective wherever possible. I say ‘whenever possible’ because you’re dealing with beings of emotion. Objectivity won’t work every time. Try throwing objectivity at your girlfriend when she’s pissed off at you for emotional reasons and see what happens. Trust me, it’s a fun experiment.</p>\n</li>\n<li>\n<p>Try to communicate in a crisp and concise manner devoid of any tones. I am still learning how to.</p>\n</li>\n<li>\n<p>Be nice to people. Saying “hello”, “thank you” etc goes a long way.</p>\n</li>\n<li>\n<p>There is a clear difference between being straightforward and being a dick. Some wise people I know made me understand the difference.</p>\n</li>\n<li>\n<p>Avoid or minimize contact with people who think the world revolves around them.</p>\n</li>\n<li>\n<p>Try to factor in an individual’s effort into something when giving feedback.</p>\n</li>\n<li>\n<p>Try not to narrativize your way through everyday life. Read this <a href=\"https://www.buzzfeed.com/annehelenpetersen/ten-long-years-of-trying-to-make-armie-hammer-happen?utm_term=.jcaxlpzvO#.odKKQmYEe\">Buzzfeed longread</a> (I promise it’s neither an oxymoron nor a listicle) if you’re unsure of what I am talking about.</p>\n</li>\n</ul>\n<h3>Books</h3>\n<p>I’ll be honest here. I am not an avid reader. I managed to read 3 books this year - <em>Outliers</em> by <em>Malcom Gladwell</em>, <em>The Defining Decade</em> by <em>Meg Ryan</em> and <em>How to win friends and influence people</em> by Dale Carnegie. The important thing about reading non-fiction books is to hopefully understand, analyze and apply them in your life.</p>\n<p>As a footnote, I would like to thank everybody involved in the past year. You know who you’re.</p>\n<p><strong>May The Force Live Long and Prosper With You!</strong></p>","frontmatter":{"title":"Looking back at 2017","date":"December 29, 2017","description":"I'll be (looking) back","category":null,"next":null,"previous":"event-bubbling-in-javascript"}}},"pathContext":{"slug":"/looking-back-at-2017/"}}

/***/ })

